.sidebar__leads-text {
  width: 100%;
  height: 2.45rem;
  margin-bottom: 0.05rem;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 0.57rem;
  text-decoration: none;
}

.sidebar__leads-text:hover {
  background-color: #ae4cbe;
  color: #fff;
}

