  padding: 0rem 0.16rem;
  cursor: pointer;
  border: none;
  outline: none;
  cursor: pointer;
  background-color: #80288f;
  font-size: 0.47rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.patient-dashboard__btn-container {
  display: flex;
  align-items: center;
}

.patient-dashboard__btn-icon {
  width: 0.39rem;
  height: 0.48rem;
  color: #fff;
  margin-right: 0.2rem;
  position: relative;
}

.patient-dashboard-refresh-btn {
  color: #fff;
  font-size: 0.47rem;
  font-weight: 500;
  margin-right: 0.5rem;
}

.patient-dashboard__followup-cards {
  margin-top: 1rem;
  padding: 0rem 0.8rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.patient-dashboard__followup-cards-container {
  width: 30.95rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1.56rem;
}

