.custom-dropdown {
  width: 2.61rem;
  height: 0.97rem;
  border-radius: 0.094rem;
  padding: 0rem 0.16rem;
  cursor: pointer;
  border: none;
  outline: none;
  cursor: pointer;
  background-color: #80288f;
  font-size: 0.47rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.3rem !important;
  color: #fff !important;
}

.p-cascadeselect {
  background-color: #802a8f;
  border-radius: 50%;
  height: 0.8rem;
  width: 0.8rem;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.36rem;
  margin-left: 0.2rem;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}
.p-cascadeselect-trigger {
  display: none;
}

.p-cascadeselect .p-cascadeselect-label.p-placeholder {
  color: #fff !important;
  font-size: 0.3rem;
  width: 0.09rem !important;
  position: relative;
  left: 0.18rem;
  top: 0.1rem;
}

.p-cascadeselect-sublist-wrapper {
  left: -115% !important;
}

.p-cascadeselect-panel .p-cascadeselect-items {
  background-color: #faf6f6 !important;
  width: 2.45rem !important;
  color: red !important;
  font-size: 0.5rem !important;
  padding: 0.2rem;
  border-radius: 0.2rem;
}

.p-cascadeselect-panel
  .p-cascadeselect-items
  .p-cascadeselect-item
  .p-cascadeselect-group-icon {
  color: #80288f !important;
  font-size: 0.01rem !important;
  width: 0.28rem !important;
}

.p-cascadeselect-panel
  .p-cascadeselect-items
  .p-cascadeselect-item.p-highlight {
  font-size: 0.4rem !important;
}

.p-cascadeselect .p-cascadeselect-label {
  font-size: 0.4rem !important;
  color: #fff !important;
}

.p-dialog-header-close-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  width: 0.3rem;
  height: 0.3rem;
}
.p-dialog-header-icons {
  width: 0.7rem !important;
  height: 0.7rem !important;
  background-color: var(--brandClr) !important;
  border-radius: 50% !important;
  padding: 0.1rem;
  margin: 0.3rem;
}

.p-dialog-header-icons:hover {
  width: 0.7rem !important;
  height: 0.7rem !important;
  background-color: var(--brandClr) !important;
  border-radius: 50% !important;
  padding: 0.1rem;
  margin: 0.3rem;
}

.p-dialog .p-dialog-header .p-dialog-header-icon {
  width: 0.7rem !important;
  height: 0.7rem !important;
  border-radius: 50% !important;
  padding: 0.1rem;
  background-color: #80288f;
}

.patient-dashboard__selectedFilters-container {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  align-items: center;
  gap: 0.3rem;
  width: 19rem;
}

.patient-dashboard__selectedFilters-container > button {
  background-color: #80288f;
  border-radius: 0.2rem;
  padding: 0.2rem 0.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.4rem;
  font-weight: 600;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
}

.patient-dashboard__selectedFilters-container > button > span {
  margin-right: 0.2rem;
}

.p-cascadeselect-item-text {
  font-size: 0.4rem !important;
  font-weight: 700 !important;
  color: #80288f !important;
}

.p-cascadeselect-sublist-wrapper .p-cascadeselect-item {
  margin-bottom: 0.3rem !important;
}

.dropdown-submenu {
  position: relative;
}

.dropdown-submenu .dropdown-menu {
  top: 0;
  left: 100%;
  margin-top: -1px;
}

.filter-popup-container {
  background-color: #faf6f7;
  color: #000;
  font-size: 0.2rem;
  padding: 0.3rem;
  border-radius: 0.3rem;
}

.popup-arrow {
  color: #faf6f7 !important;
  position: absolute;
  top: 0.01rem;
}

.filter-popup-container__inner-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 0.4rem;
}
.filter-popup-container__inner-container > h3 {
  font-size: 0.5rem;
  margin: 0.1rem;
  font-weight: 700;
  margin-bottom: 0.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #80288f;
}

.filter-popup-container__inner-container > h3 > span {
  margin-right: 0.2rem;
}

.filter-popup-container__inner-container > ul {
  padding: 0rem 0.7rem;
  margin: 0px;
  display: flex;
  flex-direction: column;
  row-gap: 0.2rem;
}

.filter-popup-container__inner-container > ul > li {
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.filter-popup-container__inner-container > ul > li > input {
  height: 0.6rem;
  border: none;
  outline: none;
  box-shadow: none;
  margin-right: 0.3rem;
}

.filter-popup-container__inner-container > ul > li > span {
  font-weight: 500;
  color: #fff;
  font-size: 0.4rem;

  /* color: #80288f; */
}

.p-dialog-content {
  overflow-y: hidden !important;
}

.skeleton-loader {
  background-color: gray;
  height: 6.8rem;
  width: 9rem;
  border-radius: 0.22rem;
  padding: 0.31rem 0.3rem;
  -webkit-animation: blink-1 2s infinite both;
  animation: blink-1 2s infinite both;
}

@-webkit-keyframes blink-1 {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0.8;
  }
}
@keyframes blink-1 {
  0%,
  50%,
  100% {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0.8;
  }
}

.skeleton-loader > div {
  -webkit-animation: blink-1 2s infinite both;
  animation: blink-1 2s infinite both;
}

#popup-text-container {
  display: none;
}

#filter-submenu-container {
  display: none;
}
.add-button:hover + #popup-text-container {
  display: block;
}

#popup-text-container:hover #filter-submenu-container {
  display: block;
}

.dropdown-menu li {
  position: relative;
  z-index: 0;
}
.dropdown-menu .dropdown-submenu {
  display: none;
  position: absolute;
  left: -118%;
  top: 0px;
}
.dropdown-menu > li:hover > .dropdown-submenu {
  display: block;
  font-size: 0.04rem !important;
  z-index: 0;
  border: solid 1px #80288f;
}

.dropdown-menu {
  cursor: pointer;
  --mdb-dropdown-min-width: 4.5rem !important;
  --mdb-dropdown-height: 1rem !important;

  height: auto !important;
  --mdb-dropdown-border-radius: 0.1rem !important;
  color: #80288f;
  --mdb-dropdown-padding-x: 0.3rem !important;
  --mdb-dropdown-padding-y: 0.3rem !important;
}

.dropdown-menu > li {
  line-height: 0.3rem;
  padding: 0.3rem 0rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0rem !important;
}

.dropdown-menu > li > span {
  font-size: 0.55rem;
  text-transform: capitalize;
  font-weight: 600;
}

.custom-button::after {
  display: none !important;
}

