.leads-main-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.leads-main-container__sub-heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.76rem;
}

.leads-main-container__sub-heading > h1 {
  font-size: 1.81rem;
  color: var(--brandClr);
  font-weight: 600;
}

.leads-main-container__table-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.leads-main-container__table > .table {
  width: 30rem;
  background-color: red;
  overflow-x: scroll;
  height: 50vh;
}

.leads-main-container__body-data {
  display: flex;
  overflow-x: scroll;
}

.leads-main-container__body-data > p::-webkit-scrollbar {
  display: none;
}

.leads-main-container__body-data > p {
  height: 20px;
  color: white;
  font-size: 0.96rem;
  font-weight: 700;
  width: 50px;
  margin: 10px;
}

.form-control {
  width: 3rem;
  height: 10px;
  border-radius: 0px;
  font-size: 10px;
  font-weight: 400;
}

tr {
  font-size: 0.4rem;
}

th {
  width: 50px;
}

.loader {
  width: 80px;
  height: 50px;
  position: relative;
}

.loader-text {
  position: absolute;
  top: 0;
  padding: 0;
  margin: 0;
  color: #c8b6ff;
  animation: text_713 3.5s ease both infinite;
  font-size: 0.8rem;
  letter-spacing: 1px;
}

.load {
  background-color: #9a79ff;
  border-radius: 50px;
  display: block;
  height: 16px;
  width: 16px;
  bottom: 0;
  position: absolute;
  transform: translateX(64px);
  animation: loading_713 3.5s ease both infinite;
}

.load::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background-color: #d1c2ff;
  border-radius: inherit;
  animation: loading2_713 3.5s ease both infinite;
}

@keyframes text_713 {
  0% {
    letter-spacing: 1px;
    transform: translateX(0px);
  }

  40% {
    letter-spacing: 2px;
    transform: translateX(26px);
  }

  80% {
    letter-spacing: 1px;
    transform: translateX(32px);
  }

  90% {
    letter-spacing: 2px;
    transform: translateX(0px);
  }

  100% {
    letter-spacing: 1px;
    transform: translateX(0px);
  }
}

@keyframes loading_713 {
  0% {
    width: 16px;
    transform: translateX(0px);
  }

  40% {
    width: 100%;
    transform: translateX(0px);
  }

  80% {
    width: 16px;
    transform: translateX(64px);
  }

  90% {
    width: 100%;
    transform: translateX(0px);
  }

  100% {
    width: 16px;
    transform: translateX(0px);
  }
}

@keyframes loading2_713 {
  0% {
    transform: translateX(0px);
    width: 16px;
  }

  40% {
    transform: translateX(0%);
    width: 80%;
  }

  80% {
    width: 100%;
    transform: translateX(0px);
  }

  90% {
    width: 80%;
    transform: translateX(15px);
  }

  100% {
    transform: translateX(0px);
    width: 16px;
  }
}

.highlight-row {
  animation: highlight_last_row 4s;
}

@keyframes highlight_last_row {
  0% {
    background-color: rgb(14, 68, 145);
    color: white;
  }

  99% {
    background-color: rgb(14, 68, 145);
  }

  100% {
    background-color: rgba(241, 247, 255);
    color: inherit;
  }
}

