.patient-details-coach-note-container {
  margin-top: 1.28rem;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.patient-details-coach-note-container > label {
  font-size: 0.37rem;
  width: 2.92rem !important;
  font-weight: 600;
  margin-right: 2.37rem !important;
}

.patient-details-coach-note-container > textarea {
  width: 17.92rem !important;
  height: 5.16rem !important;
  border: solid #000 0.01rem;
  border-radius: 0.11rem;
  background-color: #f9f9f9;
  font-weight: 600;
  font-size: 0.37rem;
  padding: 0.5rem;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

textarea:focus {
  border: none;
  outline: none;
}

option {
  font-size: 0.37rem;
  font-weight: 600;
}

.followup-table-container {
  margin-top: 0.9rem;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.followup-table-container__heading {
  font-size: 0.8rem;
  font-weight: 600;
  color: #802a8f;
  margin-top: 1rem;
